import React from 'react';
import './Tag.css';

function Tag({ tags }) {

  function renderTag(tag) {
    switch(tag) {
  
      case 'history':
        return '';

      case 'transport':
        return '';

      case 'technology':
        return '';

      case 'management':
        return '';

      case 'economy':
        return '';

      default:
        return tag;
    }
  }

  
  return (
    <>
    {
      tags.map((tag, i) => (
        <li key={i} className={`tag tag_type_${tag}`}>
          {renderTag(tag)}
        </li>
      ))
    }
    </>
  );
}

export default Tag; 